// Breakpoints
@mixin breakpoint($class) {
	@if $class == xs {
		@media (max-width: 320px) {
			@content;
		}
	} @else if $class == sm {
		@media (min-width: 480px) {
			@content;
		}
	} @else if $class == md {
		@media (min-width: 768px) {
			@content;
		}
	} @else if $class == lg {
		@media (min-width: 1024px) {
			@content;
		}
	} @else if $class == xl {
		@media (min-width: 1280px) {
			@content;
		}
	} @else if $class == xxl {
		@media (min-width: 1441px) {
			@content;
		}
	}
}
