body {
  color: #000;
  font-size: 2rem;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 300;
  line-height: 1.6;
  overflow-x: hidden; }

a {
  text-decoration: none; }
  a:focus {
    outline: thin dotted; }
  a:hover, a:active {
    outline: 0; }

code {
  display: block;
  background-color: #999; }

blockquote {
  margin: 0 1.5em; }

address {
  margin: 0 0 1.5em; }

/*--------------------------------------------------------------
# Typography
--------------------------------------------------------------*/
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  margin-bottom: 2rem;
  font-family: "Lato", sans-serif;
  font-weight: 700;
  line-height: 1.2; }
  h1 a,
  h2 a,
  h3 a,
  h4 a,
  h5 a,
  h6 a {
    color: #000;
    text-decoration: none; }
    h1 a:hover,
    h2 a:hover,
    h3 a:hover,
    h4 a:hover,
    h5 a:hover,
    h6 a:hover {
      text-decoration: underline; }

h1,
.h1,
.section-title {
  font-size: 4rem;
  font-weight: 900; }
  @media (min-width: 768px) {
    h1,
    .h1,
    .section-title {
      font-size: 5.5rem; } }

h2,
.h2 {
  font-size: 3.2rem; }
  @media (min-width: 768px) {
    h2,
    .h2 {
      font-size: 4.2rem; } }

h3,
.h3 {
  font-size: 3rem; }
  @media (min-width: 768px) {
    h3,
    .h3 {
      font-size: 3.4rem; } }

h4,
.h4 {
  font-size: 3rem; }

h5,
.h5 {
  font-size: 2.4rem; }

p {
  margin: 0;
  margin-bottom: 2rem; }

dfn,
cite,
em,
i {
  font-style: italic; }

pre {
  font-family: "Courier 10 Pitch", Courier, monospace;
  font-size: 15px;
  font-size: 0.9375rem;
  line-height: 1.6; }

code,
kbd,
tt,
var {
  font-family: Monaco, Consolas, "Andale Mono", "DejaVu Sans Mono", monospace;
  font-size: 15px;
  font-size: 0.9375rem; }

big {
  font-size: 125%; }

/* Table styles */
/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
*:focus {
  outline: dotted 0.1rem rgba(0, 0, 0, 0.3); }

/* Text meant only for screen readers. */
.screen-reader-text {
  border: 0;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute !important;
  width: 1px;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */ }

.screen-reader-text:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  clip-path: none;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 1.5rem 2.3rem 1.4rem;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
  /* Above WP toolbar. */ }

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
  outline: 0; }
